.cart{
    padding: 100px 10px;
    background-color: #f8f4f4;
    min-height: 100vh;
}

.cart .container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.cart .container .card{
    width: 400px;
    padding: 10px 20px;
    border: 2px solid #ffffff;
    margin: auto;
    margin-top: 20px;
}
.cart .container .card .image{
    height: 200px;
    width: 100%;
    border: 1px solid #ffffff;
}
.cart .container .card .image img{
    width: 100%;
    height: 100%;
}
.cart .container .card .about{
    margin-top: 10px;
}
.cart .container .card .about h3{
    font-size: 25px;
    font-weight: 600;
    color: blue;
    text-transform: uppercase;
}
.cart .container .card .about p{
    font-size: 18px;
    line-height: 25px;
    font-weight: 400;
    color: rgb(122, 122, 122);
}
@media(max-width: 767px){
    .cart .container{
        grid-template-columns:  1fr;
    }
}