.container{
    padding: 100px 50px;
    background-color: #fbfbfb;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 2em;
}
@media(max-width: 767px){
    .container{
        grid-template-columns: 1fr 1fr;
    }
}
@media(max-width: 520px){
    .container{
        grid-template-columns: 1fr;
    }
}